export const nexAutoplayDateIsUndefined = 'next autoplay date is undefined.';
export const gcpBadConfiguration = (gcpConfig) => `gcpDatastoreInteractorConfiguration bad configuration : ${JSON.stringify(gcpConfig)}`;
export const saveRecordStart = (path) => `⚙️  saveRecordOnGoogleDatastore - ${path}`;
export const saveRecordSuccess = (path) => `✔️  Entity with key path ${path} saved on datastore.`;
export const saveRecordError = (error) => `❌  ${error.message}`;
export const deleteRecordStart = (path) => `⚙️  deleteRecordOnGoogleDatastore - ${path}`;
export const deleteRecordSuccess = (path) => `✔️  Entity with key path ${path} deleted on datastore.`;
export const deleteRecordError = (error) => `❌  ${error.message}`;
export const retreiveRecordStart = (path) => `⚙️  retreiveRecordOnGoogleDatastore - ${path}`;
export const retreiveRecordSuccess = (path) => `✔️  Entity with key path ${path} retreived from datastore.`;
export const retreiveRecordError = (error) => `❌ ${error.message}`;
export const queryRecordsStart = (kind) => `⚙️  queryRecordsOnGoogleDatastore - ${kind} `;
export const queryRecordsFilter = (filter) => `⚙️  ${filter.property}${filter.operator}${filter.value}`;
export const queryRecordsSuccess = (entities, kind) => `✔️  ${entities.length} entities retrieved on kind ${kind} according to filters.`;
export const queryRecordsError = (filter) => `❌ Filter ${filter.value} Error`;
export const gcpQueryOffsetStart = (query) => `⚙️  GCP Query Start - ${query.kinds} - ${query.offsetVal} - ${query.limitVal}`;
export const retreiveRecordByOffsetErrorMultipleEntities = (kind, offset) => `❌ Multiple entities of kind '${kind}' at offset '${offset}' ?!`;
export const retreiveRecordByOffsetErrorNoEntity = (kind, offset) => `❌ No entity of kind '${kind}' at offset '${offset}'`;
export const entityRetreivedAtOffset = (entities, query) => `✔️  ${entities.length} entities retrieved on kind '${query.kinds}' at offset '${query.offsetVal}'.`;
export const cleavageUndefined = 'Cleavage is undefined.';
